<template>
  <div class="comment-wrapper">
    <!-- <p class="title">精彩评论</p> -->
    <div class="comment-item">
      <div class="head">
        <img :src="commentData.avatarurl" alt="" />
        <div class="time-wrapper">
          <div class="time">{{ commentData.nick }}</div>
          <div class="nickName">
            {{ new Date(parseInt(commentData.time)).toLocaleString().replace('/','年').replace('/','月').replace(' ','日 ') }}
          </div>
          <div class="root-content">
            {{ commentData.rootcommentcontent }}
          </div>
          <div class="score">
            <span class="liked">
              <van-icon name="good-job-o" />
              {{ commentData.score }}
            </span>
            <span class="reply">
              回复
            </span>
            <span class="report">举报</span>
          </div>
          <div class="childComment" v-if="commentData.middlecommentcontent">
            <span>查看{{ commentData.middlecommentcontent.length }}条回复</span>
          </div>
        </div>
      </div>
      
    </div>
  </div>
</template>

<script>
export default {
  props: {
    commentData: Object,
  },
};
</script>

<style lang="less" scoped>
.comment-wrapper {
  width: 1000px;
  margin: 20px;
  
  .title {
  }
  .head {
    display: flex;
    img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      border: 1px solid #e5e5e5;
    }
    .time-wrapper {
      font-size: 13px;
      color: #646566;
      margin: 1px 15px;
      .nickName {
        margin-top: 5px;
      }
      .root-content{
        margin-top: 20px;
        font-size: 16px;
        color: #111;
      }
      .score{
        margin-top: 20px;

        .liked{
          font-size: 20px;
          color: #111;
          margin-right: 10px;
          cursor: pointer;
        }
        .liked:hover{
          color: #1ECD95;
        }
        .reply{
          margin-right: 10px;
          cursor: pointer;
        }
        .reply:hover{
          color: #1ECD95;
        }
        .report{
          visibility: hidden;
          cursor: pointer;
        }
        .report:hover{
          color: #1ECD95;
        }
      }
      .score:hover{
        .report{
          visibility: visible;
          
        }
      }
      .childComment{
        margin-top: 10px;
        cursor: pointer;
      }
    }
  }
}
</style>