<template>
  <div class="video-wrapper">
    <div class="videoPage">
      <div class="head">
        <h1>视频详情</h1>
        <video :src="url" class="video" controls v-if="url.length"></video>

        <!-- <video controls v-if="url.length" class="video">
          <source :src="url" type="video/mp4" />
        </video> -->
      </div>
      <div class="detail" v-if="videoDetail">
        <div class="author-wrapper">
          <div class="author">
            <img :src="videoDetail.avatarUrl" alt="" />
            <span class="name" v-if="videoDetail.creator">{{
              videoDetail.creator.nickname
            }}</span>
          </div>

          <van-button
            v-if="!isAttention"
            round
            type="primary"
            icon="add"
            color="#1ed2a8"
            @click="attention"
            >关注</van-button
          >
          <van-button
            v-else
            round
            type="primary"
            icon="add"
            color="#1ed2a8"
            @click="attention"
            >取消关注</van-button
          >
        </div>
        <p class="title">{{ videoDetail.title }}</p>
        <div class="publish">
          <span
            >发布:{{
              new Date(parseInt(videoDetail.publishTime))
                .toLocaleString()
                .replace(/:\d{1,2}$/, " ")
            }}</span
          >
          <span style="margin-left: 20px"
            >播放量:{{
              videoDetail.playTime > 10000
                ? Math.ceil(videoDetail.playTime / 10000) + "万"
                : videoDetail.playTime
            }}</span
          >
        </div>
        <div class="tag">
          <p>
            标签:
            <span
              style="color: #5091ca"
              v-for="(item, index) in videoDetail.videoGroup"
              :key="index"
              class="spTags"
              >{{
                index == videoDetail.videoGroup.length - 1
                  ? item.name
                  : item.name + "/"
              }}</span
            >
          </p>
        </div>
        <div class="comment">
          <p>精彩评论</p>
          <div class="comment-item" v-for="item in commentlist" :key="item">
            <Comment :commentData="item" />
            <van-divider
              :style="{ borderColor: '#969799', padding: '0 19px' }"
            />
          </div>
        </div>
      </div>
    </div>
    <div class="songList-wrapper">
      <span style="margin-left:8px">相关推荐</span>
      <div class="song-list">
        <div
          class="song-item"
          v-for="item in videoList"
          :key="item"
          @click="toPlayList(item.vid)"
        >
          <div class="top">
            <img :src="item.coverUrl" alt="" />
            <div class="listenCount" v-if="item.coverUrl">
              <i class="bi bi-play"></i>
              <span v-if="item.playTime / 10000 > 10000"
                >{{ (item.playTime / 100000000).toFixed(1) }}亿</span
              >
              <span v-else>{{ (item.playTime / 10000).toFixed(1) }}万</span>
            </div>
            <div class="playTime">
              {{
                String(new Date(item.durationms).getMinutes()).padStart(
                  2,
                  "0"
                ) +
                ":" +
                String(new Date(item.durationms).getSeconds()).padStart(2, "0")
              }}
            </div>
          </div>
          <div class="bottom">
            <div class="title">{{ item.title }}</div>
            <span v-if="item.creator">by {{ item.creator[0].userName }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, reactive, toRefs, watch } from "vue";
import { useRoute, useRouter } from "vue-router";
import axios from "axios";
import { Toast } from "vant";
import Comment from "@/components/Comment";
export default {
  components: {
    Comment,
  },
  setup() {
    const state = reactive({
      url: "",
      id:'',
      isAttention: false,
      videoDetail: {},
      recommendList: [],
      commentlist: [],
      videoList: [],
    });
    watch(()=>state.url,(newVal)=>{
      console.log(newVal);
      state.url = newVal
    })
    const route = useRoute();
    const router = useRouter()
    const attention = () => {
      state.isAttention = !state.isAttention;
      state.isAttention ? Toast("成功关注") : Toast("取消关注");
    };
    const toPlayList = (id)=>{
      reqApi(id)
      router.push({ name: "videoPage", query: { id: id } });
    }
    const reqApi = (id)=>{
      axios({
        url: `/mini/video/url?id=${id}`,
      }).then((res) => {
        if (res.data.code == 200) {
          state.url = res.data.urls[0].url;
        }
      });
      axios({
        url: `/mini/video/detail?id=${id}`,
      }).then((res) => {
        if (res.data.code == 200) {
          state.videoDetail = res.data.data;
        }
      });
      axios({
        url: `/mini/comment/video?id=${id}`,
      }).then((res) => {
        if (res.data.code == 200) {
          state.commentlist = res.data.hotComments.map((el) => {
            el.avatarurl = el.user.avatarUrl;
            el.nick = el.user.nickname;
            el.rootcommentcontent = el.content;
            el.score = el.likedCount;
            return el;
          });
        }
      });
      axios({
        url: `/mini/related/allvideo?id=${id}`,
      }).then((res) => {
        state.videoList = res.data.data;
        console.log(state.videoList);
      });
    }
    onMounted(() => {
      state.id = route.query.id;
      reqApi(state.id)
    });
    return {
      ...toRefs(state),
      attention,
      toPlayList,
      reqApi
    };
  },
};
</script>

<style lang="less" scoped>
.video-wrapper {
  display: flex;
  .videoPage {
    margin-left: 40px;
    width: 600px;
    .head {
      h1 {
        margin: 0;
      }
      .video {
        width: 600px;
        height: 360px;
      }
    }
    .detail {
      width: 600px;
      margin-top: 20px;
      .author-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .author {
          img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            vertical-align: middle;
          }
        }
        button {
          height: 40px;
        }
      }
      .title {
        font-size: 30px;
      }
      .publish {
        font-size: 14px;
        color: grey;
      }
    }
  }
  .songList-wrapper {
    margin-top: 20px;
    margin-left: 30px;
    .song-list {
      width: 100%;
      .song-item {
        cursor: pointer;
        margin: 10px;
        width: 317px;
        border-radius: 10px;
        display: flex;
        .top {
          position: relative;
          img {
            width: 150px;
            border-radius: 10px;
          }
          .listenCount {
            position: absolute;
            left: 74px;
            top: 1px;
            color: #fff;
            width: 81px;
            height: 26px;
            line-height: 26px;
            text-align: center;
            font-size: 14px;
            border-radius: 10px;
          }
          
          .playTime {
            position: absolute;
            left: 74px;
            bottom: 1px;
            color: #fff;
            width: 81px;
            height: 26px;
            line-height: 26px;
            text-align: center;
            font-size: 14px;
            border-radius: 10px;
          }
          
        }
        .bottom {
          margin-left: 8px;
          .title {
            font-size: 14px;
          }
          span {
            font-size: 12px;
            color: grey;
          }
        }
      }
      .song-item:hover {
        transform: translateY(-12px);
        transition: 0.4s;
        .listenCount {
          display: none;
        }
        .playTime{
          display: none;
        }
      }
    }
  }
}
</style>